import { Component, OnInit } from '@angular/core';
import { NavController } from '@ionic/angular';
import { CommonStorage } from '../services';

@Component({
  selector: 'app-ticket-cancel',
  templateUrl: './ticket-cancel.page.html',
  styleUrls: ['./ticket-cancel.page.scss'],
})
export class TicketCancelPage implements OnInit {
  logo: any;
  constructor(private navCtrl:NavController,private commonStorage : CommonStorage) { }

  ngOnInit() {
    this.logo = this.commonStorage.localGet("metaData").mobileLoginPageLogo?this.commonStorage.localGet("metaData").mobileLoginPageLogo: this.commonStorage.getItem("metaData").headerLogo;
  }
  goToHome(){
    this.navCtrl.navigateRoot('tabs/home')
  }

}
